import "./styles.css";
import Child from "./Child";
import { useRef } from "react";
export default function App() {
  const ref = useRef(null);
  const hc = () => {
    ref.current.focus();
    ref.current.style.color = "red";
    ref.current.value = 100;
  };
  return (
    <div className="App">
      <Child ref={ref} />
      <button onClick={() => hc()}>btn</button>
    </div>
  );
}
